@charset "UTF-8";
@import "../utility/variable";
@import "../utility/function";

@font-face {
    font-family: 'HumbleHearts';
    font-weight: 400;
    src: url('../../font/HumbleHearts.woff')  format('woff'),
    url('../../font/HumbleHearts.ttf')  format('truetype');
}

@font-face {
    font-family: 'PetitFormalScript';
    font-weight: 400;
    src: url('../../font/PetitFormalScript-Regular.woff')  format('woff'),
    url('../../font/PetitFormalScript-Regular.ttf')  format('truetype');
}

$HumbleHearts:'HumbleHearts';
$PetitFormalScript:'PetitFormalScript';

$c-pink:#d46b80;
$c-pink-light:#ebb5bd;

// -----------------------------------------------------------------
// HEAD
// -----------------------------------------------------------------

.p-rct{
    &-wrap{
        width:100%;
        overflow: hidden;
    }

    &-head{
        display: flex;
        justify-content: center;
        align-items: flex-end;
        text-align: center;
        width: 100%;
        padding-bottom: pxtovw(108);
        height: pxtovw(644);
        background: url(../../img/recruit/bg_head.jpg.webp) no-repeat 50%;
        background-size: cover;
        @include mq(sm,max){
            align-items: center;
            padding-bottom: 0;
            height: pxtovw_sp(538);
            background-image: url(../../img/recruit/bg_head_sp.jpg.webp);
        }
        .no-webp &{
            background-image: url(../../img/recruit/bg_head.jpg);
            @include mq(sm,max){
                background-image: url(../../img/recruit/bg_head_sp.jpg);
            }
        }
        &__ttl {
            display: inline-flex;
            font-size: pxtovw(120);
            color: #fff;
            font-family: $fontEn;
            font-weight: 600;
            line-height:.75;
            position: relative;
            @include mq(sm,max){
                font-size: pxtovw_sp(65);
            }
            span{
                position: absolute;
                right:-1em;
                bottom:-.5em;
                z-index: 1;
                font-weight: 400;
                font-size: pxtovw(95);
                line-height: 1;
                rotate:-10deg;
                font-family:$HumbleHearts;
                color:$c-pink-light;
                text-shadow: 0 0 .05em $c-pink;
                white-space: nowrap;
                @include mq(sm,max){
                    font-size: pxtovw_sp(84.67);
                    right:auto;
                    left: 50%;
                    bottom:-.65em;
                    translate: -50% 0;
                }
            }
        }
    }

// -----------------------------------------------------------------
// GREETING
// -----------------------------------------------------------------

&-enttl{
    font-size: pxtovw(16);
    color: rgba(#fff,.6);
    font-family: $fontEn;
    font-weight: 600;
    line-height:.75;
    letter-spacing: .1em;
    margin-bottom:pxtovw(24);
    @include mq(sm,max){
        font-size: pxtovw_sp(14); 
        margin-bottom:pxtovw_sp(20);
        padding-left:pxtovw_sp(7);
    }
}

&-ttl{
    display: inline-block;
    line-height: 1.7;
    background-color: #fff;
    padding-inline: pxtovw(20);
    box-decoration-break: clone;	
	-webkit-box-decoration-break: clone;
    @include mq(sm,max){
        display:inline;
        padding:pxtovw_sp(24) pxtovw_sp(7) pxtovw_sp(7);
    }
    span{
        font-size: pxtovw(40);
        font-weight: bold;
        letter-spacing: .1em;
        background: linear-gradient(90deg, #f4a062 0%, #eb907b 50%, #e3818e 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        @include mq(sm,max){
            font-size: pxtovw_sp(30);            
        }
    }
}

&-txt{
    margin-top:pxtovw(24);
    line-height: 1.7;
    font-weight: bold;
    font-size: pxtovw(24);
    color:#fff;
    letter-spacing: .14em;
    @include mq(sm,max){
        padding-left:pxtovw_sp(7);
        font-size: pxtovw_sp(16);
        margin-top:pxtovw_sp(24);
        letter-spacing: .04em;
    }
    span{
        display: inline-block;
        border:1px solid;
        line-height: 1.5;
        padding:0 .2em;
        margin:.25em .2em;
        &.--ml0{
            margin-left: 0;
        }
    }
}

&-greeting{
    width:100%;
    padding:pxtovw(96) pxtovw(60) pxtovw(180);
    text-align: center;
    background: url(../../img/recruit/bg_greeting.jpg.webp) no-repeat 50%;
    background-size: cover;
    @include mq(sm,max){
        text-align: left;
        padding:pxtovw_sp(50) pxtovw_sp(20) pxtovw_sp(110);
        background-image: url(../../img/recruit/bg_greeting_sp.jpg.webp);
    }
    .no-webp &{
        background-image: url(../../img/recruit/bg_greeting.jpg);
        @include mq(sm,max){
            background-image: url(../../img/recruit/bg_greeting_sp.jpg);
        }
    }
}


// -----------------------------------------------------------------
// HIRING
// -----------------------------------------------------------------

&-hiring{
    width:100%;
    padding-top: pxtovw(90);
    margin-top: pxtovw(-90);
    border-radius:pxtovw(70) pxtovw(70) 0 0;
    background-color: #fff;
    position: relative;
    z-index: 1;
    @include mq(sm,max){
        padding-top: pxtovw_sp(56);
        margin-top: pxtovw_sp(-70);
        border-radius:pxtovw_sp(20) pxtovw_sp(20) 0 0;
    }
    &__enttl{
        width:100%;
        text-align: center;
        position: absolute;
        left: 0;
        top:-.45em;
        z-index: 2;
        font-weight: 400;
        font-size: pxtovw(105);
        line-height: 1;
        rotate:-10deg;
        font-family:$HumbleHearts;
        color:$c-pink;
        text-shadow: 0 0 .05em $c-pink-light;
        @include mq(sm,max){
            font-size: pxtovw_sp(60.75);
            top:-.35em;
        }
    }
    &__ttl{
        display: flex;
        align-items: center;
        justify-content: center;
        gap:pxtovw(15);
        text-align: center;
        margin-bottom: pxtovw(10);
        line-height: 1;
        font-size: pxtovw(24);
        font-weight: bold;
        letter-spacing: .14em;
        @include mq(sm,max){
            gap:pxtovw_sp(10);
            margin-bottom: pxtovw_sp(8);
            font-size: pxtovw_sp(18);
        }
        &::before,&::after{
            content:"";
            width:pxtovw(13);
            aspect-ratio: 13.073 / 23.452;
            background-repeat: no-repeat;
            background-size:cover;
            @include mq(sm,max){
                width:pxtovw_sp(9);
            }
        }
        &::before{
            background-image: url(../../img/recruit/ico_hiring_l.svg);
        }
        &::after{
            background-image: url(../../img/recruit/ico_hiring_r.svg);
        }
    }
    &__list{
        width:100%;
        max-width:pxtovw(638);
        margin-inline:auto;
        @include mq(sm,max){
            max-width:pxtovw_sp(294);
        }
        li{
            width:100%;
            border-bottom:1px dotted #353535;
            a{
                display: flex;
                align-items: center;
                gap:pxtovw(15);
                color:#353535;
                padding:pxtovw(24) pxtovw(10) pxtovw(10) 0;
                @include mq(sm,max){
                    flex-wrap: wrap;
                    gap:pxtovw_sp(8);
                    padding:pxtovw_sp(16) pxtovw_sp(6) pxtovw_sp(8) pxtovw_sp(8);
                }
                time{
                    width:pxtovw(115);
                    font-size: pxtovw(14);
                    letter-spacing: .2em;
                    line-height: 1.2;
                    @include mq(sm,max){
                        width:100%;
                        font-size: pxtovw_sp(12);
                    }
                }
                p{
                    flex:1;
                    font-size: pxtovw(16);
                    line-height: 1.2;
                    letter-spacing: .2em;
                    @include mq(sm,max){
                        font-size: pxtovw_sp(14);
                        letter-spacing: .14em;
                    }
                }
                &::after{
                    content:"";
                    width:pxtovw(22);
                    aspect-ratio: 22.057 / 5.564;
                    margin-left: auto;
                    background: url(../../img/recruit/ico_arrow.svg) no-repeat;
                    background-size: cover;
                    @include mq(sm,max){
                        width:pxtovw_sp(16);
                    }
                }
            }
        }
    }
}

&-menu{
    display: flex;
    justify-content: center;
    gap:pxtovw(20);
    width:100%;
    margin-top: pxtovw(50);
    padding-block:pxtovw(40);
    background:url(../../img/recruit/bg_squares.jpg.webp) repeat-x;
    background-size: auto 100%;
    @include mq(sm,max){
        gap:pxtovw_sp(20);
        flex-wrap: wrap;
        padding:0 pxtovw_sp(36);
        margin-top: pxtovw_sp(40);
        background-position: 0 50%;
        background-size: auto pxtovw_sp(248);
        background-image: url(../../img/recruit/bg_squares_sp.jpg.webp);
    }
    .no-webp &{
        background-image: url(../../img/recruit/bg_squares.jpg);
        @include mq(sm,max){
            background-image: url(../../img/recruit/bg_squares_sp.jpg);
        }
    }
    li{
        width:pxtovw(160);
        aspect-ratio: 1;
        position: relative;
        z-index: 1;
        @include mq(sm,max){
            width:calc(50% - pxtovw_sp(10));
        }
        &::before{
            content:"";
            width:100%;
            height: 100%;
            border-radius:inherit;
            background: linear-gradient(90deg, #f4a062 0%, #eb907b 50%, #e3818e 100%);
            position: absolute;
            right:pxtovw(-4);
            bottom:pxtovw(-4);
            border-radius:pxtovw(16);
            z-index: -2;
            @include mq(sm,max){
                right:pxtovw_sp(-4);
                bottom:pxtovw_sp(-4);
                border-radius:pxtovw_sp(12);    
            }
        }
        a{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            width:100%;
            height: 100%;
            text-align: center;
            border-radius:pxtovw(16);
            gap:pxtovw(16);
            background-color: #fff;
            border:2px solid #f4a062;
            border-right-color: #e3818e;
            position: relative;
            z-index: 1;
            @include mq(sm,max){
                border-radius:pxtovw_sp(12);    
                gap:pxtovw_sp(10);
            }
            &::after{
                content:"";
                width:pxtovw(35);
                aspect-ratio: 35.062 / 8.508;
                background: url(../../img/recruit/ico_arrow_pink.svg) no-repeat;
                background-size: cover;
                @include mq(sm,max){
                    width:pxtovw_sp(30);
                }
            }
        }
        span{
            font-size: pxtovw(16);
            font-weight: bold;
            line-height: 1.25;
            letter-spacing: .14em;
            background: linear-gradient(90deg, #f4a062 0%, #eb907b 50%, #e3818e 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            @include mq(sm,max){
                font-size: pxtovw_sp(16);
            }
        }
    }
}

// -----------------------------------------------------------------
// CHARM
// -----------------------------------------------------------------

&-charm{
    width:100%;
    margin-top: pxtovw(40);
    padding-top:pxtovw(100);
    text-align: center;
    background:url(../../img/recruit/bg_charm.jpg.webp) no-repeat 50% 0;
    background-size: 100% pxtovw(588);
    @include mq(sm,max){
        margin-top: pxtovw_sp(50);
        padding-top:pxtovw_sp(80);
        background-size: 100% pxtovw_sp(394);
        background-image: url(../../img/recruit/bg_charm_sp.jpg.webp);
    }
    .no-webp &{
        background-image: url(../../img/recruit/bg_charm.jpg);
        @include mq(sm,max){
            background-image: url(../../img/recruit/bg_charm_sp.jpg);
        }
    }
    &__inner{
        display: flex;
        flex-direction: column;
        gap:pxtovw(60);
        width:100%;
        max-width:pxtovw(1005);
        margin:pxtovw(60) auto 0;
        padding:pxtovw(60) pxtovw(60) 0;
        background-color: #fff;
        @include mq(sm,max){
            gap:pxtovw_sp(36);
            max-width:100%;
            margin-top:pxtovw_sp(46);
            padding:0;    
        }
    }
    &__item{
        display: flex;
        width:pxtovw(809);
        margin-inline:auto;
        text-align: left;
        gap:pxtovw(40);
        position: relative;
        z-index: 1;
        @include mq(sm,max){
            flex-direction: column-reverse;
            width:100%;
            gap:pxtovw_sp(24);
            padding:pxtovw_sp(168) pxtovw_sp(14) 0;
            &:nth-child(4){
                .p-rct-charm__item__enttl{
                    font-size: pxtovw_sp(50);
                }
            }
        }
        &::before{
            content:"";
            width:pxtovw(260);
            aspect-ratio: 260 / 248;
            background: url(../../img/recruit/bg_char_squares.jpg.webp) no-repeat 50%;
            background-size: cover;
            position: absolute;
            top:0;
            z-index: -1;
            @include mq(sm,max){
                width:pxtovw_sp(260);
            }
            .no-webp &{
                background-image: url(../../img/recruit/bg_char_squares.jpg);
                @include mq(sm,max){
            
                }
            }
        }
        &:nth-child(odd){
            &::before{
                right:pxtovw(-30);
                @include mq(sm,max){
                    right:0;
                }
            }
        }
        &:nth-child(even){
            &::before{
                left:pxtovw(-60);
                @include mq(sm,max){
                    left:0;
                }
            }
        }
        @include mq(sm,min){
            &:nth-child(odd){
                flex-direction: row-reverse;
                justify-content: space-between;
                padding-left: pxtovw(30);
            }
            &:nth-child(1){
                .p-rct-charm__item__img{
                    padding-top: pxtovw(60);
                }
                .p-rct-charm__item__txtfield{
                    padding-top: pxtovw(60);
                }
                .p-rct-charm__item__enttl span{
                    right:-.15em;
                    top:-.35em;
                }
            }
            &:nth-child(2){
                .p-rct-charm__item__img{
                    padding-top: pxtovw(30);
                }
                .p-rct-charm__item__enttl span{
                    right:-.3em;
                    top:.3em;
                    rotate:10deg;
                }
            }
            &:nth-child(3){
                padding-top: pxtovw(20);
                .p-rct-charm__item__enttl span{
                    right:-.45em;
                    top:-.12em;
                }
            }
            &:nth-child(4){
                .p-rct-charm__item__img{
                    padding-top: pxtovw(20);
                }
                .p-rct-charm__item__ttl{
                    margin-top: pxtovw(80);
                }
                .p-rct-charm__item__enttl span{
                    right:.22em;
                    top:.27em;
                }
            }
            &:nth-child(5){
                .p-rct-charm__item__img{
                    padding-top: pxtovw(20);
                }
                .p-rct-charm__item__enttl span{
                    right:-.5em;
                    top:-.1em;
                }
            }
        }
        &__txtfield{
            width:pxtovw(386);
            @include mq(sm,max){
                width:100%;
            }
        }

        &__enttl{
            display: inline-block;
            font-size: pxtovw(64);
            font-weight: 400;
            font-family:$HumbleHearts;
            white-space: nowrap;
            color:$c-pink;
            text-shadow: 0 0 .05em $c-pink-light;
            rotate:-10deg;
            line-height: 1;
            position: relative;
            z-index: 1;
            @include mq(sm,max){
                display: flex;
                align-items: center;
                justify-content: center;
                width:100%;
                height: pxtovw_sp(168);
                position: absolute;
                top:0;
                left:0;
                font-size: pxtovw_sp(64.1);
            }
            span{
                color:rgba(#c5c5c5,.3);
                font-size: pxtovw(150);
                letter-spacing: .1em;
                font-family: $PetitFormalScript;
                text-shadow: none;
                line-height: 0.7;
                position: absolute;
                @include mq(sm,max){
                    font-size: pxtovw_sp(150);
                }
            }
        }
        &__ttl{
            font-size: pxtovw(24);
            font-weight: bold;
            line-height: 1.3;
            letter-spacing: .14em;
            margin: pxtovw(34) 0 pxtovw(14);
            @include mq(sm,max){
                font-size: pxtovw_sp(24);
                margin: 0 0 pxtovw_sp(16);
                letter-spacing: .04em;
                text-align: center;
            }
            span{
                color:#d98992;
            }
        }
        &__txt{
            font-size: pxtovw(14);
            line-height: 1.7;
            letter-spacing: .14em;
            @include mq(sm,max){
                font-size: pxtovw_sp(14);
                line-height: 1.64;
            }
        }
        &__img{
            width:pxtovw(345);
            @include mq(sm,max){
                width:100%;
            }
        }
    }
}

// -----------------------------------------------------------------
// INTERVIEW
// -----------------------------------------------------------------

&-intvw{
    width:100%;
    margin-top: pxtovw(110);
    padding-top:pxtovw(100);
    text-align: center;
    background:url(../../img/recruit/bg_interview.jpg.webp) no-repeat 50% 0;
    background-size: 100% pxtovw(588);
    @include mq(sm,max){
        margin-top: pxtovw_sp(70);
        padding-top:pxtovw_sp(78);
        background-size: 100% pxtovw_sp(394);
        background-image: url(../../img/recruit/bg_interview_sp.jpg.webp);
    }
    .no-webp &{
        background-image: url(../../img/recruit/bg_interview.jpg);
        @include mq(sm,max){
            background-image: url(../../img/recruit/bg_interview_sp.jpg);
        }
    }
    &__inner{
        display: flex;
        justify-content: center;
        width:100%;
        background-color: #fff;
        margin-top: pxtovw(68);
        padding-top: pxtovw(100);
        border-radius:pxtovw(70) pxtovw(70) 0 0;
        @include mq(sm,max){
            margin-top: pxtovw_sp(46);
            padding: pxtovw_sp(50) pxtovw_sp(14) 0;
            border-radius:pxtovw_sp(20) pxtovw_sp(20) 0 0;   
        }
    }
    &__bnr{
        display: block;
        width:pxtovw(1003);
        @include mq(sm,max){
            width:100%;
        }
    }
}

// -----------------------------------------------------------------
// MOVIE
// -----------------------------------------------------------------

&-movie{
    width:100%;
    margin-top: pxtovw(110);
    padding-block:pxtovw(120);
    text-align: center;
    background:url(../../img/recruit/bg_movie.jpg.webp) no-repeat 50% 0;
    background-size: 100% pxtovw(588);
    @include mq(sm,max){
        margin-top: pxtovw_sp(50);
        padding:pxtovw_sp(80) 0 pxtovw_sp(54);
        background-size: 100% pxtovw_sp(394);
        background-image: url(../../img/recruit/bg_movie_sp.jpg.webp);
    }
    .no-webp &{
        background-image: url(../../img/recruit/bg_movie.jpg);
        @include mq(sm,max){
            background-image: url(../../img/recruit/bg_movie_sp.jpg);
        }
    }
    &__list{
        display: flex;
        width:pxtovw(846);
        gap:pxtovw(18);
        margin:pxtovw(70) auto pxtovw(110);
        overflow: auto;
        @include mq(sm,max){
            width:100%;
            gap:pxtovw_sp(14);
            margin:pxtovw_sp(46) 0 pxtovw_sp(90);
            padding-inline:pxtovw_sp(14);
            overflow: auto;
        }
        li{
            width:pxtovw(270);
            @include mq(sm,max){
                width:pxtovw_sp(236);
                min-width:pxtovw_sp(236);
            }
            a{
                display: block;
                width: 100%;
            }
        }
    }
    &__wtc{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width:pxtovw(700);
        margin-inline:auto;
        padding:pxtovw(40) pxtovw(66) pxtovw(40) pxtovw(76);
        background:url(../../img/recruit/bg_movie_watch.jpg.webp) no-repeat 50% 0;
        background-size: cover;
        position: relative;
        @include mq(sm,max){
            flex-direction: column;
            width:100%;
            gap:pxtovw_sp(20);
            padding:pxtovw_sp(30) pxtovw_sp(14);
        }
        .no-webp &{
            background-image: url(../../img/recruit/bg_movie_watch.jpg);
        }
        &__enttl{
            position: absolute;
            top:-.3em;
            left:-.4em;
            z-index: 1;
            font-weight: 400;
            font-size: pxtovw(69.5);
            line-height: 1;
            rotate:-10deg;
            font-family:$HumbleHearts;
            color:$c-pink;
            text-shadow: 0 0 .05em $c-pink-light;
            @include mq(sm,max){
                font-size: pxtovw_sp(60.75);
                top:-.8em;
                left: 0;
                width:100%;
                text-align: center;
            }
        }
        &__ttl{
            color:#fff;
            font-weight: bold;
            font-size: pxtovw(24);
            text-align: left;
            line-height: 1.3;
            letter-spacing: .14em;
            @include mq(sm,max){
                font-size: pxtovw_sp(18);
            }
        }
        &__list{
            display: flex;
            align-items: center;
            justify-content: center;
            width:pxtovw(348);
            padding:pxtovw(30);
            gap:pxtovw(40);
            border-radius:pxtovw(16);
            background-color: #fff;
            @include mq(sm,max){
                width:100%;
                padding:pxtovw_sp(24);
                gap:pxtovw_sp(30);
                border-radius:pxtovw_sp(14);
            }
            li{
                width:pxtovw(68);
                @include mq(sm,max){
                    width:pxtovw_sp(56);            
                }
                a{
                    display: block;
                    width:100%;
                }
            }
        }
    }
}


// -----------------------------------------------------------------
// NAVI
// -----------------------------------------------------------------

&-navi{
    display: flex;
    position: fixed;
    z-index: 50;
    @include mq(sm,min){
        flex-direction: column;
        right:0;
        top:50%;
        translate: 0 -50%;
    }
    @include mq(sm,max){
        left:0;
        bottom:0;
        width:100%;
    }
    &__item{
        width:68px;
        height: 218px;
        @include mq(sm,max){
            width:33.33333%;
            height: 70px;
        }
        a{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            gap:10px;
            width:100%;
            height: 100%;
            border-radius:10px 0 0 10px;
            @include mq(sm,max){
                gap:6px;
                border-radius:10px 10px 0 0;
            }
            span{
                font-size:18px;
                font-weight: bold;
                color:#fff;
                letter-spacing: .1em;
                @include mq(sm,min){
                    -ms-writing-mode: tb-rl;
                    writing-mode: vertical-rl;
                    text-orientation: upright;
                }
                @include mq(sm,max){
                    line-height: 1;
                    font-size: 14px;
                }
            }
            &::before{
                content:"";
                width:36px;
                aspect-ratio: 1;
                background-repeat: no-repeat;
                background-size:cover;
                @include mq(sm,max){
                    width:32px;
                }
            }
        }
        &--entry{
            background-color: #353535;
            &::before{
                background-image: url(../../img/recruit/ico_entry.svg);
            }
        }
        &--line{
            background-color: #008c26;
            &::before{
                background-image: url(../../img/recruit/ico_line.svg);
            }
        }
        &--company{
            background-color: #b78f65;
            &::before{
                background-image: url(../../img/recruit/ico_pdf.svg);
            }
        }
    }
}


}
